.inner-block {
	min-height: 1000px;
	display: flex;
	flex-direction: column;
	gap: 32px;

	&__content {
		margin-left: auto;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		gap: 32px;
		text-transform: uppercase;

		p {
			font-size: 24px;
			font-weight: 500;
			@media(max-width: 767px) {
				font-size: 16px;
			}
		}
	}
}