.staff {
	.swiper-slide {
		display: flex;
		flex-direction: column;
		gap: 24px;
		padding: 32px;
		background-color: #202020;
		border-radius: 25px;

		// height: auto;
		@media(max-width: 767px) {
			padding: 24px 16px;
		}
	}
}

.staff__subtitle {
	font-size: 16px;
	font-weight: 500;
	margin-bottom: 32px;

	@media(max-width: 767px) {
		font-size: 14px;
	}
}

.staff__pic {
	height: 196px;

	img {
		border-radius: 5px;
		height: 100%;
		width: 100%;
		object-fit: cover;
	}
}

.staff__container {
	position: relative;
	padding: 0 74px;

	@media(max-width: 767px) {
		padding: 0;
	}
}

.staff__swiper {
	position: static;

	.swiper-wrapper {
		@media(max-width: 767px) {
			margin-bottom: 24px;
		}
	}

	@media(max-width: 767px) {
		position: relative;
		padding-bottom: 76px;
	}
}

.staff__swiper-title {
	font-size: 20px;
	font-weight: 700;

	br {
		@media(max-width: 767px) {
			display: none;
		}
	}

	@media(max-width: 767px) {
		font-size: 18px;
	}
}

.staff__head {
	display: flex;
	flex-direction: column;
	gap: 18px;
	min-height: 90px;

	@media(max-width: 767px) {
		min-height: auto;
	}
}

.staff__body {
	display: flex;
	flex-direction: column;
	flex: 1;
	min-height: 120px;

	span {
		font-size: 18px;
		font-weight: 700;

		@media(max-width: 767px) {
			font-size: 14px;
		}
	}

	p {
		font-size: 16px;

		&:not(:last-child) {
			margin-bottom: 20px;
		}

		@media(max-width: 767px) {
			font-size: 14px;
		}
	}

	@media(max-width: 767px) {
		min-height: auto;
	}
}

.staff__spec {
	margin-top: auto;
	min-height: 118px;

	span {
		font-size: 18px;
		font-weight: 700;

		@media(max-width: 767px) {
			font-size: 14px;
		}
	}

	@media(max-width: 767px) {
		margin-top: 0;
		min-height: auto;
	}
}

.staff__footer {
	display: flex;
	flex-direction: column;
	gap: 6px;

	span {
		&:first-child {
			font-size: 52px;
			font-weight: 700;
		}

		&:last-child {
			font-size: 16px;
		}
	}
}

.staff__swiper-exp {
	@media(max-width: 767px) {
		font-size: 14px;
	}
}

.staff__content {
	display: flex;
	flex-direction: column;
	gap: 24px;
	// min-height: 230px;

	@media(max-width: 767px) {
		gap: 18px;
	}
}