.main-block {
	position: relative;
	overflow: hidden;
	padding: 80px 0;
}

.main-block__img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -1;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	@media(max-width: 767px) {
		height: 763px;
	}
}

.main-block__wrap {
	display: flex;
	flex-direction: column;
	// min-height: 1368px;
	min-height: 858px;
}

.main-block__top-content {
	display: grid;
	align-items: center;
	grid-template-columns: 1fr 388px;

	@media(max-width: 1023px) {
		display: flex;
		flex-direction: column;
		gap: 18px;
		align-items: flex-start;
	}
}

.main-block__title {
	font-size: 134px;
	font-weight: 500;
	text-transform: uppercase;
	line-height: 1;

	@media(max-width: 767px) {
		font-size: 62px;
	}
}

.main-block__aside {
	display: flex;
	flex-direction: column;

	@media(max-width: 767px) {
		width: 100%;
	}
}

.main-block__aside-block {
	display: flex;
	flex-direction: column;
	gap: 6px;
	margin-bottom: 30px;

	span {
		&:first-child {
			font-size: 24px;
			font-weight: 500;
			text-transform: uppercase;

			@media(max-width: 767px) {
				font-size: 14px;
			}
		}

		&:nth-child(2) {
			font-size: 20px;
			font-weight: 400;

			@media(max-width: 767px) {
				font-size: 12px;
			}
		}
	}
}

.main-block__benefici {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 32px;
	margin-top: auto;

	@media(max-width: 767px) {
		display: flex;
		flex-direction: column;
		// margin-top: 213px;
		margin-top: 185px;
	}
}

.main-block__benefici-item {
	padding: 32px;
	border-radius: 25px;
	display: flex;
	flex-direction: column;
	min-height: 350px;
	border: 1px solid #ffffff;

	@media(max-width: 767px) {
		min-height: auto;
	}
}

.main-block__benefici-title {
	font-size: 20px;
	font-weight: 500;
	text-transform: uppercase;

	@media(max-width: 767px) {
		font-size: 16px;
	}
}

.main-block__benefici-text {
	margin-top: auto;

	p {
		@media(max-width: 767px) {
			font-size: 14px;
		}
	}

	p:not(:last-child) {
		margin-bottom: 20px;
	}

	@media(max-width: 767px) {
		margin-top: 32px;
	}
}