.offer__wrap {
	// display: flex;
}

.offer__aside {
	position: relative;
	display: flex;
	flex-direction: column;
}

.offer__list {
	display: flex;
	flex-direction: column;
	gap: 37px;

	&-item {
		display: flex;
		flex-direction: column;
		padding-bottom: 24px;

		&:not(:last-child) {
			border-bottom: 1px solid #fff;
		}

		&:last-child {
			// max-width: 600px;
		}
	}

	&-title {
		font-size: 16px;
		font-weight: 700;
		text-transform: uppercase;
		padding-bottom: 18px;

		@media(max-width: 767px) {
			font-size: 14px;
			padding-bottom: 12px;
		}

		&--mode {
			padding-bottom: 0;
		}
	}

	&-text {
		font-size: 16px;
		font-weight: 500;
		margin-bottom: 20px;
		// color: rgba(255, 255, 255, 0.6);
		color: white;

		@media(max-width: 767px) {
			font-size: 14px;
		}
	}

	@media(max-width: 767px) {
		margin-bottom: 24px;
	}
}